
.App {
  text-align: center;
}

.App-logo {
  height: 50px;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #00ee4d;
  height: 80px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  color: white;
  padding: 0 20px;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

form {
  text-align: left;
}

.label {
  display: block;
  text-transform: uppercase;
  text-align: left;
}

.container {
  padding: 50px 20px;
  max-width: 1020px;
  margin: 0 auto;
  text-align: left;
}

.hide {
  opacity: 0;
  height: 0;
  overflow: hidden;
  transition: opacity 1s, height 1s;
}

.input-pass {
  width: 192px;
}

button {
  font-weight: bold;
  background-color: #00ee4d;
  border: 0;
  padding: 5px 10px;
  display: block;
  margin: 10px 0;
  text-transform: uppercase;
  width: 200px;
}